@import '../../variables.scss';

.navigation-wrap {
    width: 100%;
    min-height: 100vh;
    height: fit-content;
    opacity: 1;
    animation-name: fade2;
    animation-duration: .2s;
    position: relative;
    
    .navigation-wrapper {
        width: 100%;
        height: $nav-height;
        background-position: 0% 11%;
        
        .navigation {
            width: 100%;
            height: 100%;
            padding: 0 100px;
            display: flex;
            justify-content: space-around;
            align-items: center;
            background-color: rgba(255, 255, 255, 0);
            backdrop-filter: blur(5px);
            -webkit-box-shadow: 0px 7px 23px 4px rgba(0, 0, 0, 0.4);
            -moz-box-shadow: 0px 7px 23px 4px rgba(0, 0, 0, 0.4);
            box-shadow: 0px 7px 23px 4px rgba(0, 0, 0, 0.4);

            .nav-horizontal, div {
                display: flex;
                align-items: center;

                h1 {
                    font-size: 1.5rem;
                    font-weight: 400;
                    color: #e6e3d0;
                }
            }

            .nav-open-button {
                display: none;
            }
            

            .logo {
                width: 160px;
                margin-right: 30px;
            }

            .home-icon {
                margin-top: 3px;
            }
            
            a {
                margin: 0 20px;
                transition: .2s;
                // font-weight: 600;
                color: #cfbeac;
                
                &:hover {
                    color: #e6e3d0;
                }
            }
        }
    }
    .nav-vertical {
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.7);
        position: fixed;
        top: 0;
        left: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        z-index: 999999;

        .nav-vertical-container {
        }

        a {
            margin: 20px 0;
            color: #cfbeac;
        }
    }
}

@keyframes fade2 {
    0% {
        opacity: 0;
    }
    
    100% {
        opacity: 1;
    }
}

@media (max-width: 1150px) {
    .navigation-wrap {
        .navigation-wrapper {
            .navigation {
                padding: 0 40px;
    
                .nav-horizontal, .logo-wrapper {
                    h1 {
                        font-size: 1.2rem;
                    }
                }
    
                .logo {
                    width: 160px;
                    margin-right: 15px;
                }
    
                a {
                    margin: 0 10px;
                    font-size: .8rem;
                }
            }
        }
    }
}

@media (max-width: 900px) {
    .navigation-wrap {
        .navigation-wrapper {
            .navigation {
                display: flex;
                justify-content: space-around;
                align-items: center;
                padding: 0 10px;
    
                .nav-horizontal, .logo-wrapper {
                    h1 {
                        font-size: 1.1rem;
                    }
                }
    
                .logo {
                    width: 130px;
                    margin-right: 10px;
                }
    
                a {
                    margin: 0 10px;
                    font-size: .8rem;
                }
            }
        }
    }
}

@media (max-width: 600px) {
    .navigation-wrap {
        .navigation-wrapper {
            .navigation {
                position: relative;
                padding: 0 40px;

                .nav-horizontal, .logo-wrapper {
                    h1 {
                        font-size: 1.1rem;
                    }
                }
    
                .nav-horizontal {
                    display: none;
                }

                .nav-open-button {
                    display: flex;
                }
            }
        }
    }
}

@media (max-width: 420px) {
    .navigation-wrap {
        .navigation-wrapper {
            .navigation {

                justify-content: space-between;
                padding: 0 20px;

                .logo {
                    width: 160px;
                    margin-right: 10px;
                }
    
                a {
                    margin: 0 10px;
                    font-size: .8rem;
                }
            }
        }
    }
}